  height: 15rem;
  object-fit: contain;
  background: #F6F5F0;
  /* padding:20px; */
  /* padding: 20px 0px 20px 0px; */
  border-top-left-radius: var(--mainBorderRadius);
  border-top-right-radius: var(--mainBorderRadius);
}

.cocktail-footer {
  padding: 15px;
  display: flex;
  flex-direction: column;
  background: rgb(0, 0, 0);
}

.cocktail-footer h3,
.cocktail-footer h4,
